import React from "react"
import Header from "../components/Header"
import styled from "styled-components"

import "../components/layout.css"

const SkillsSection = styled.section`
  text-align: center;
  margin: 50px 0;
`
const SkillsTitle = styled.h1`
  font-size: 70px;
  margin-bottom: 10px;
  @media (min-width: 768px) and (max-width: 1024px) {
    font-size: 64px;
  }
  @media (min-width: 200px) and (max-width: 767px) {
    font-size: 46px;
  }
`

const SkillsHolder = styled.section`
  max-width: 800px;
  margin: 1rem auto;
`

const SkillsTechnical = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  letter-spacing: 1px;
  margin: 40px 0;
`

const SkillsSoft = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  letter-spacing: 1px;
`

const SkillsTechnicalTitle = styled.h2``

const SkillsSoftTitle = styled.h2``

const Skills = styled.p`
  margin: 0.5rem;
  border-radius: 4px;
  background: #252d4f;
  padding: 0.5rem;
`
const skills = () => {
  return (
    <>
      <Header />

      <div className="wrapper">
        <SkillsSection>
          <SkillsTitle>Skills</SkillsTitle>
          <SkillsHolder>
            <SkillsTechnical>
              <SkillsTechnicalTitle>Technical Skills: </SkillsTechnicalTitle>
              <Skills>HTML5</Skills>
              <Skills>CSS3</Skills>
              <Skills>Javascript (ES6+)</Skills>
              <Skills>React</Skills>
              <Skills>Redux</Skills>
              <Skills>Apollo Client</Skills>
              <Skills>SASS</Skills>
              <Skills>Firebase</Skills>
              <Skills>Git</Skills>
              <Skills>GraphQL</Skills>
              <Skills>Gatsby</Skills>
            </SkillsTechnical>
            <SkillsSoft>
              <SkillsSoftTitle>Soft Skills: </SkillsSoftTitle>
              <Skills>Critical-Thinking</Skills>
              <Skills>Organization</Skills>
              <Skills>Dependable</Skills>
              <Skills>Problem-Solver</Skills>
              <Skills>Team-Player</Skills>
            </SkillsSoft>
          </SkillsHolder>
        </SkillsSection>
      </div>
    </>
  )
}

export default skills
